import {
    CoreEnvelopeBuilder,
    CoreEnvelopeContentsRaw,
    LogMessageRaw,
    StaticAttributes
} from 'o11y/core_envelope';
import { LazyMapToList, SchematizedData, schemaUtil, utility } from 'o11y/shared';
import {
    LogCollector,
    LogMeta,
    MetricsCollector,
    MetricsExtractorMethods,
    Schema
} from '../interfaces';
import {
    PrecollectCallbackType,
    SimpleCollectorOptions
} from './interfaces/SimpleCollectorOptions';

const defaultMaxUniqueSchemas = 10000;

export class SimpleCollector implements LogCollector, MetricsCollector {
    private readonly _messageBuffers: LazyMapToList<Schema, LogMessageRaw>;
    private readonly _staticAttributes: StaticAttributes;
    private readonly _precollectCallback: PrecollectCallbackType;
    private _metricsExtractors: MetricsExtractorMethods;
    private _messageSize = 0;
    private _isCollecting = false;

    constructor(options?: SimpleCollectorOptions) {
        let maxUniqueSchemas = defaultMaxUniqueSchemas;
        if (options) {
            const env = options.environment;
            if (env) {
                this._staticAttributes = {
                    appName: env.appName,
                    appVersion: env.appVersion,
                    appExperience: env.appExperience,
                    deviceId: env.deviceId,
                    deviceModel: env.deviceModel,
                    sdkVersion: env.sdkVersion
                };
            }

            if (
                utility.requireArgumentIfDefined(
                    options.precollectCallback,
                    'options.precollectCallback',
                    'function'
                )
            ) {
                this._precollectCallback = options.precollectCallback;
            }

            const mus: number = options.maxUniqueSchemas;
            if (mus !== undefined) {
                if (typeof mus !== 'number' || !(mus > 0)) {
                    throw new Error('options.maxUniqueSchemas, if defined, must be > 0');
                }
                maxUniqueSchemas = mus;
            }
        }
        this._messageBuffers = new LazyMapToList<Schema, LogMessageRaw>(maxUniqueSchemas);
    }

    collect(schema: Schema, data: SchematizedData, logMeta: LogMeta): void {
        if (this._isCollecting) {
            return;
        }
        this._isCollecting = true;
        try {
            const msg: LogMessageRaw = {
                timestamp: logMeta.timestamp,
                data,
                age: logMeta.age,
                rootId: logMeta.rootId,
                seq: logMeta.sequence,
                sseq: logMeta.schemaSequence,
                loggerName: logMeta.loggerName,
                pagePayload: logMeta.pagePayload,
                loggerAppName: logMeta.loggerAppName,
                connectionType: logMeta.connectionType,
                appPayload: logMeta.appPayload,
                csId: logMeta.clientSessionId,
                recCsId: logMeta.receiverClientSessionId,
                recRootId: logMeta.receiverRootId
            };

            if (this._precollectCallback?.(schema, msg) === false) {
                return;
            }

            if (!this._messageBuffers.push(schema, msg)) {
                throw new Error(
                    `Buffer is full. Refusing schemaId ${schemaUtil.getSchemaId(schema)}`
                );
            }
            this._messageSize += utility.estimateObjectSize(msg);
        } finally {
            this._isCollecting = false;
        }
    }

    get estimatedByteSize(): number {
        return this._messageSize + this.metricsCount * 8;
    }

    get hasData(): boolean {
        return this.messagesCount > 0 || this.metricsCount > 0;
    }

    get messagesCount(): number {
        return this._messageBuffers.totalItemCount;
    }

    get metricsCount(): number {
        let count = 0;

        if (this._metricsExtractors) {
            count += this._metricsExtractors.getAllUpCounters().length;
            count += this._metricsExtractors.getAllValueRecorders().length;
            count += this._metricsExtractors.getAllBucketHistograms().length;
        }

        return count;
    }

    getRawContentsOfCoreEnvelope(): CoreEnvelopeContentsRaw {
        const contents: CoreEnvelopeContentsRaw = {
            staticAttributes: this._staticAttributes,
            messages: this._messageBuffers.getAllMessages(true)
        };

        if (this._metricsExtractors) {
            contents.upCounters = CoreEnvelopeBuilder.getUpCounters(
                this._metricsExtractors.getAllUpCounters(),
                true
            );
            contents.valueRecorders = CoreEnvelopeBuilder.getValueRecorders(
                this._metricsExtractors.getAllValueRecorders(),
                true
            );
            contents.bucketHistograms = CoreEnvelopeBuilder.getBucketHistograms(
                this._metricsExtractors.getAllBucketHistograms(),
                true
            );
        }

        this._messageSize = 0;
        return contents;
    }

    receiveMetricsExtractors(metricsExtractors: MetricsExtractorMethods): void {
        this._metricsExtractors = metricsExtractors;
    }
}
